import '../css/Introduce.css';

export default function Introduce(){
    return(
        <>
        <section id="intro">
            <div id="imain">
                <h1>INTRODUCE<br/>ME</h1>
                <div id="intro1">
                    <h2>Growth process</h2>
                    <p>
                        <b>선택, 그리고 나</b>
                        어느 날 티비를 보다가  ‘사람들은 살면서 매 순간 선택을 하며 그 선택에는 책임감이 따른다’  이 문구를 보는 순간 깨달았습니다. 한편으로는 지금까지 살아오면서 나에게도 많은 선택의 순간이 있었구나, 나를 위한 선택이었는지, 또  선택에 있어 책임감 있게 헤쳐나갔는지 에 대한 의심도 들었습니다. 이러한 의심은 찰나였습니다. 돌이켜 생각해보면 지금까지 다양한 선택지와 그에 따른 결과와 경험으로 인해 성장할 수 있었다고 생각합니다. 다양한 활동을 통해 어떠한 일이 재밌고 잘 할 수 있는지에 대한 느낌표를 찍을 수 있는 순간들이 있어 지금의 저를 성장하게 했습니다. 살아오면서 많은 것을 배웠고 그것에 안주하지 않고 계속 공부하면서 나의 것으로 만드는 과정들이 저를 더 단단하게 만들었습니다. 
                    </p>
                </div>
            </div>
            
            <div id="intro2">
                <h2>
                    Character Strengths
                    <span></span>
                </h2>
                <div id="introtxt2">
                    <p>
                        <b>소통,책임감을 가진 개발자</b>
                        대학생 때 우연한 계기로 2년 동안 학생회 활동을 하게 되면서 책임감과 소통능력을 배울 기회였습니다.
                        학생회 활동과 수업, 아르바이트 등으로 스스로 시간을 쪼개서 할 일들을 정리하고 그 기간 안에 마무리하려고 노력하고 학생들과 소통으로 바쁘게 대학 시절을 보냈지만, 그러면서 많은 것을 깨닫고 아쉬운 부분들을 해가 거듭될수록 만족할 수 있도록 성장할 수 있는 소중한 기회였습니다.
                        어떠한 일이든, 어느 회사에 소속되어 있든지 혼자 힘으로는 성장시킬 수 없다는 것을 깨닫고 서로가 각자의 위치에서 맡은 일을 하며 소통을 통해 목표를 향해 좋은 성과를 낼 수 있다고 생각했습니다.<br/>

                        회사에서 업무가 주어질 때 나의 것으로 만들어서 처음부터 결과까지 도달 할 수 있도록 책임감과 소통을 통해 진행할 수 있습니다. 대학 전공이 디자인분야기 때문에 포토샵, 일러스트를 기본적으로 사용할 수 있습니다. 프런트 엔드 개발 업무를 진행할 때 퍼블리셔, 디자이너분들과 작업할 때 원활한 소통과 결과물이 완성될 때 좋은 시너지 효과를 볼 수 있습니다. 개발자 공부를 하면서 부족한 부분들이 있어 그것을 최대한 나의 것으로 만들려고 노력하고 코드가 하나하나 완성될 때, 만들어진 코드가 잘 작동될 때 성취감을 느낄 수 있었습니다. 저에게 주어진 일을 책임감 있게 결과까지 만들 수 있도록 진행되는 모든 일이 저에겐 한 걸음 더 성장 할 수 있고 발전할 가능성이 크다고 생각합니다.  소통하며 책임감 있는 개발자가 되겠습니다.
                    </p>
                </div>
            </div>
            <div id="intro3">
            <h2>
                Reason for application
                <span></span>
            </h2>
                <div id="introtxt3">
                    <div id="introtxtleft">
                        <p>
                            <b>나의 것으로 만들 수 있는 개발자</b>
                            사용하는 모든 사람이 불편하고, 효율적이지 않은 코드라면 사람들에게는 잊혀질 코드라고 생각합니다.
                            말하고자 하는 방향과 사용하고자 하는 사람들이 편리하고 효율적으로 서버를 만들면 사람들에게는 손이 기억하게 되고 그것 또한 하나의 마케팅이라 생각합니다.<br/><br/>

                            어떠한 일을 개발하려고 할 때 기초부터 결과까지 단계가 있어야하고  기초가 탄탄하지 않으면 결과까지 가더라도 중간에 무너질 수 있기 때문에 한 단계별로 최대한 완벽해야 원하는 결과물을 만들어 낼 수 있다고 생각합니다. 프론트엔드 개발자 교육을 받으면서 많은 것을 배울 수 있었고 공부할수록 프론트엔드 개발자에 대한 매력을 느꼈습니다.
                        </p>
                    </div>
                    <div id="introtxtright">
                        <p>교육과정 중 기존사이트를 리뉴얼해 보완하는 방향으로 작업을 진행했을 때 사이트를 사용하는 사람들에게 효율적이고 편리한 사이트를 만들 수 있도록 분석하며 단계별 계획을 세워 결과물을 만들었습니다. 그 과정에서 물론 부족했던 부분도 있었고, 어려운 부분이 있었지만, 포기하지 않고 부족했던 부분을 찾아보고, 물어보면서  사이트를 만들어 냈을 때의 뿌듯함은 이루 말할 수 없었습니다. 작업을 진행하면서 많은 것을 깨달았고, 공부할 수 있었습니다.<br/><br/>

                        귀사에서 채용의 기회를 얻게 된다면, 어떠한 일을 진행할 때 계획을 세워 분석하고, 소통을 통해 해결해 나갈 수 있는 사람이 되겠습니다. 계속 공부하고 나의 것으로 만들어 낼 수 있는 개발자가 되겠습니다. 감사합니다.</p>
                    </div>
                </div>
            </div>
        </section>
        </>
    );
}